___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  font-family: Verdana, sans-serif;
}

#content {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Nav Bar */
#nav-bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 0 0 0;
  gap: 13px;
  height: 7vh;
}

/* Logo */
#nav-bar .logo {
  font-weight: bold;
  font-size: 2em;
}

#nav-bar .logo:hover {
  cursor: pointer;
}

/* List */
#nav-bar ul {
  display: flex;
  list-style-type: none;
  justify-content: space-evenly;
  gap: 50px;
  font-size: 1.2em;
}

#nav-bar ul li:hover {
  text-decoration: underline;
  cursor: pointer;
}

#nav-bar ul li:active {
  text-decoration: none;
}

/* Main Body */
#main-content {
  flex: 1;
  position: relative;
}

#main-content::before {
  content: '';
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.7;
}

/* Information section */
#main-content .information {
  position: relative;
  background: rgba(228, 223, 223, 0.8);
  margin: 1.5em;
  padding: 1.2em;
  height: 80vh;

  display: flex;
  flex-direction: column;
  align-items: center;
